



























import { Vue, PropSync, Component } from "vue-property-decorator";

@Component
export default class ColorPickerPopup extends Vue {
  @PropSync("color", { type: String }) syncedColor!: string;

  data() {
    return {
      menu: false
    };
  }

  get swatchStyle() {
    return {
      backgroundColor: this.syncedColor,
      cursor: "pointer",
      height: "30px",
      width: "30px",
      borderRadius: "4px"
    };
  }
}
